import Calendar from './Calendar.svelte';
const createCalendar = (target, { placeholder = '-', blank, selectedDate, startMonth, endMonth, selectInitialDate = false, onSelect }) => new Calendar({
    target: target,
    props: {
        onSelect,
        placeholder,
        blank,
        selectedDate: selectedDate || new Date,
        startMonth,
        endMonth,
        selectInitialDate
    }
});
export default createCalendar;
